
.cover-tips {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  text-align: center;
}
.cover-tips .box {
  position: relative;
  top: 50%;
  left: 50%;
  width: 2.9333rem;
  height: 1.2rem;
  transform: translateX(-50%) translateY(-50%);
  background-color: #fff;
  border-radius: 0.0667rem;
}
.cover-tips .box p {
  width: 90%;
  margin: auto;
  color: #555;
  font-size: 0.2133rem;
}
.cover-tips .box .btn-right {
  margin-top: 0.1333rem;
  font-size: 0.2667rem;
}
.cover-tips .middle {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}


