
.comp-index {
  position: relative;
  height: 100%;
}
.comp-index >header {
  height: 50PX;
  background-color: #fff;
}
.comp-index >header .logo {
  width: auto;
  height: 100%;
}
.comp-index >.content {
  position: absolute;
  top: 50PX;
  bottom: 0;
  left: 0;
  right: 0;
  background-size: cover;
  background-image: url("../assets/pc/bg.jpg");
}
.comp-index >.content .title {
  position: absolute;
  top: 45%;
  left: 0;
  transform: translateY(-50%);
  width: auto;
  height: 180PX;
}


