
html,
body {
  margin: 0;
  width: 100%;
  height: 100%;
  font-size: 62.5%;
  font-family: '微软雅黑', '黑体';
}
input {
  outline: none;
}
p {
  margin: 0;
}
body {
  font-size: 0.2rem;
}
img {
  width: 100%;
}
.sred {
  color: #ed2285;
}
.sbtn {
  width: 2.6667rem;
  color: #fff;
  font-size: 0.16rem;
  text-align: center;
  line-height: 3em;
  background-image: url("./assets/btn.png");
  background-size: 100% 100%;
}
.btn-right {
  display: inline-block;
  width: 1em;
  height: 1em;
  background-image: url("./assets/btn-right.png");
  background-size: 100% 100%;
}
.clearfix {
  zoom: 1;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
html {
  margin: auto;
  max-width: 375PX;
}
body {
  background-color: #fff;
}
#app {
  position: relative;
  min-height: 600PX;
  padding-bottom: 0.8rem;
}
@media screen and (min-width: 800px) {
html {
    max-width: none;
}
html #app {
    height: 100%;
    padding-bottom: 0;
}
}


