
.compInput {
  position: absolute;
  top: 45%;
  right: 50PX;
  transform: translateY(-50%);
  width: 300PX;
  overflow: hidden;
  border-radius: 8PX;
  background-color: rgba(255,255,255,0.6);
}
.compInput .tabs {
  color: #fff;
  font-size: 18PX;
  line-height: 50PX;
  background-color: #f90f85;
}
.compInput .tabs .tab {
  display: inline-block;
  position: relative;
  width: 50%;
  text-align: center;
  cursor: pointer;
}
.compInput .tabs .tab.active:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100PX;
  height: 4PX;
  background-color: #fff;
}
.compInput .tabs .tab:first-child:before {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 1PX;
  height: 35PX;
  background-color: #fff;
}
.compInput .content {
  padding-bottom: 30PX;
}
.compInput .content .group {
  width: 250PX;
  margin: auto;
  margin-top: 20PX;
  padding: 5PX 8PX;
  border: 1PX solid #fff;
  border-radius: 5PX;
}
.compInput .content .group >i,
.compInput .content .group >input {
  float: left;
}
.compInput .content .group >input {
  width: 220PX;
  margin-left: 5PX;
  line-height: 20PX;
  text-align: center;
  background-color: transparent;
  border: none;
}
.compInput .content .group >input::-webkit-input-placeholder {
  color: #fff;
}
.compInput .content .group >input:-moz-placeholder {
  color: #fff;
}
.compInput .content .group >input:-ms-input-placeholder {
  color: #fff;
}
.compInput .content .option {
  text-align: center;
}
.compInput .content .option .btn-right {
  font-size: 30PX;
  opacity: 0;
}
.compInput .content .option .btn-right.active {
  opacity: 1;
}
.compInput .content .option .sbtn {
  width: 250PX;
  margin: auto;
  font-size: 12PX;
}
.compInput .content .option .tips {
  margin-top: 5PX;
  color: #666;
  font-size: 12PX;
}
.compInput .icon {
  display: inline-block;
  width: 20PX;
  height: 20PX;
  background-size: contain;
}
.compInput .icon1 {
  background-image: url("../assets/icon/1.png");
}
.compInput .icon2 {
  background-image: url("../assets/icon/2.png");
}
.compInput .icon3 {
  background-image: url("../assets/icon/3.png");
}
.compInput .icon4 {
  background-image: url("../assets/icon/4.png");
}
.compInput .icon5 {
  background-image: url("../assets/icon/5.png");
}


